
import { defineComponent, reactive } from 'vue'
import { useRouter } from "vue-router"


interface PropsEdit {
  address: string
  id: number
  isDefault: boolean
  name: string
  tel: string
}

export default defineComponent({
  name: 'petsAddress',
  components: {
  },
  setup(props, context){
    const state = reactive({
      chosenAddressId: "1",
      list: [{
          id: '1',
          name: '张三',
          tel: '13000000000',
          address: '浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室',
          isDefault: true,
        },
        {
          id: '2',
          name: '李四',
          tel: '1310000000',
          address: '浙江省杭州市拱墅区莫干山路 50 号',
      }],
      disabledList: [
        {
          id: '3',
          name: '王五',
          tel: '1320000000',
          address: '浙江省杭州市滨江区江南大道 15 号',
        },
      ],
    })
    const router = useRouter()
    const onClickLeft = () => {
      router.go(-1)
    }

    // 新增地址
    const handleAdd = () => {
      router.push("addAddress")
    }

    const handleEdit = (item: any, index: number) => {
      router.push({
        path: "editAddress",
        query: {
          list:  JSON.stringify(item)
        }
      })
    }

    return {
      state,
      handleAdd,
      handleEdit, 
      onClickLeft
    }
  }

})
